/* @import-normalize; bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div{
  padding: 20px;
}

form{
  padding: 20px;
  display: flex;
  width: 30%;
  flex-direction: column;
  align-items: flex-start;
  border: 2px black solid;
}
h1{
  font-size: 30px;
}

h2{
  font-size: 25px;
}

label{
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
}

button {
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  padding: 2px 22px;
  min-width: 100px;


  background-color: white;
  font-size: 16px;
  line-height: 1.87;
  display: flex;
  text-align: center;
  letter-spacing: 0.06em;
  cursor: pointer;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1)
  color 250ms cubic-bezier(0.4, 0, 0.2, 1);;
}

button:hover,
button:focus {
  background-color: blue;
  color: white;
}

.main_button{
  padding: 5px 22px;

}

input{
padding: 5px 22px;
margin-top: 10px;

border: 1px solid rgba(0, 0, 0, 0.15);
background-color: white;
font-size: 16px;
line-height: 1.87;
display: flex;
text-align: start;
letter-spacing: 0.06em;

}
.item_cont{
  display: flex;
  justify-content:flex-start;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  padding: 0;

  font-size: 20px;
  font-weight: bold;
}

